var ScrollBar = require('./scroll-bar.js');

var ScrollBarV = ScrollBar.extend({
    mode: "v",
    type: {
        className: "tg-scrollbar-v",
        offset: "top",
        size: "height",
        page: "pageY",
        axis: "y",
        mouseOffset: "mouseOffsetY"
    }
});

module.exports = ScrollBarV;