var Util = require("./util.js");
var OptionBase = require("./option-base.js");

var Cache = OptionBase.extend({

    previousTimestamp: 0,

    defaultOption: function() {
        return {
            //default expires time 1d / 24 hours
            expiresTime: 24 * 60 * 60 * 1000,
            //default max size
            maxSize: 1000
        };
    },

    constructor: function(option) {
        this.setOption(option);
        this.init();
    },

    //=============================================================================

    init: function() {
        this.initExpiresTime();
        this.initMaxSize();
        this.empty();
        return this;
    },

    initExpiresTime: function() {
        var expiresTime = Util.tonum(this.option.expiresTime, true);
        this.expiresTime = this.getExpiresTime(expiresTime);
        return this;
    },

    getExpiresTime: function(expiresTime) {
        //20 ms < expiresTime < 24 hours * 60 * 60 * 1000
        expiresTime = Math.max(20, Math.min(24 * 60 * 60 * 1000, expiresTime));
        return expiresTime;
    },

    initMaxSize: function() {
        var maxSize = Util.tonum(this.option.maxSize, true);
        //2 < maxSize < 10000
        this.maxSize = Math.max(2, Math.min(10000, maxSize));
        return this;
    },

    empty: function() {
        this.data = {};
        return this;
    },

    //=============================================================================

    key: function(arr) {
        return arr.join("_");
    },

    //=============================================================================

    get: function(key) {
        var item = this.data[key];
        if (!item) {
            return null;
        }
        //check item expires
        var now = new Date().getTime();
        if (now > item.expires) {
            this.remove(key);
            return null;
        }

        item.count += 1;
        return item.data;
    },

    set: function(key, data, expiresTime) {
        if (!key) {
            return this;
        }

        if (Util.isnum(expiresTime)) {
            expiresTime = this.getExpiresTime(expiresTime);
        } else {
            expiresTime = this.expiresTime;
        }
        var now = new Date().getTime();
        var expires = now + expiresTime;

        var item = {
            expires: expires,
            count: 0,
            data: data
        };

        //previous
        if (this.data[key]) {
            this.remove(key);
        }

        //add
        this.data[key] = item;

        this.clean(now);

        return this;
    },

    //=============================================================================

    remove: function(key) {
        delete this.data[key];
        return this;
    },

    removeByPrefix: function(prefix) {
        if (!prefix) {
            return this;
        }
        var keys = Object.keys(this.data);
        for (var i = 0, l = keys.length; i < l; i++) {
            var key = keys[i];
            if (key.indexOf(prefix) === 0) {
                this.remove(key);
            }
        }
        return this;
    },

    //=============================================================================

    clean: function(now) {
        if (now - this.previousTimestamp < 1000) {
            return this;
        }
        this.previousTimestamp = now;

        this.cleanByExpires();
        this.cleanBySize();

        return this;
    },

    cleanByExpires: function() {
        var now = new Date().getTime();
        var keys = Object.keys(this.data);
        for (var i = 0, l = keys.length; i < l; i++) {
            var key = keys[i];
            var item = this.data[key];
            if (now > item.expires) {
                this.remove(key);
            }
        }
        return this;
    },

    cleanBySize: function() {
        var keys = Object.keys(this.data);
        var size = keys.length;
        if (size < this.maxSize) {
            return this;
        }

        //sort by count
        var list = [];
        for (var i = 0; i < size; i++) {
            var key = keys[i];
            var item = this.data[key];
            list.push({
                key: key,
                count: item.count
            });
        }
        list.sort(function(a, b) {
            return a.count - b.count;
        });

        list.length -= this.maxSize;

        for (var j = 0, l = list.length; j < l; j++) {
            this.remove(list[j].key);
        }

        return this;

    },

    //=============================================================================

    destroy: function() {
        this.empty();
    },

    toString: function() {
        return "[object Cache]";
    }
});

module.exports = Cache;