//var Util = require('../../core/util.js');

var EventBase = require("../../../core/event-base.js");

var EditorBase = EventBase.extend({

    type: "",

    completed: false,

    constructor: function(option) {
        this.holder = option.holder;
        this.rowItem = option.rowItem;
        this.columnItem = option.columnItem;
        this.initData();
    },

    initData: function() {
        //init
        this.id = this.columnItem.id;
        this.dataType = this.columnItem.dataType;
        //original value
        this.value = this.rowItem[this.id] || "";
    },

    updateData: function(rowItem, columnItem) {
        this.rowItem = rowItem;
        this.columnItem = columnItem;
        this.initData();
        this.updateValue();
    },

    render: function() {

    },

    focus: function() {

    },

    updateValue: function() {

    },

    getValue: function() {
        return this.value;
    },

    applyValue: function() {
        var newValue = this.getValue();
        this.rowItem[this.columnItem.id] = newValue;
    },

    isValueChanged: function() {
        var newValue = this.getValue();
        if (newValue === this.value) {
            return false;
        }
        return true;
    },

    validate: function(value) {
        return {
            valid: true,
            msg: ""
        };
    },

    commit: function(e) {
        if (this.completed) {
            return this;
        }
        this.completed = true;
        this.trigger("onCommit", e);
    },

    destroy: function() {
        if (this.container) {
            this.container.remove();
            this.container = null;
        }
    },

    toString: function() {
        return this.type;
    }

});

module.exports = EditorBase;